@use "abstracts/mixins/break-word" as *;

ul, ol {
  margin: 1rem 0 1rem 1.5rem;
  padding: 0;
  line-height: 1.6;
  color: var(--fgColor-default);
  list-style-position: outside;

  ul,
  ol {
    margin: 0.5rem 0 0.5rem 1.5rem;
  }

  li {
    margin-bottom: 0.5rem;
    @include break-word();

    // + li {
    //   margin-top: 0.25em;
    // }

    &::marker {
      color: var(--fgColor-accent);
    }

    p {
      margin-bottom: inherit;
    }
  }
}

dl {
  padding: 0;
  margin-top: 0;
  margin-bottom: 1rem;

  dt {
    padding: 0;
    margin-top: 1rem;
    font-size: 1em;
    font-style: italic;
    font-weight: 600;
  }

  dd {
    padding-inline: 1rem;
    margin-bottom: 1rem;

    > * {
      @include break-word();
    }
  }
}

.task-list {
  .task-list-item {
    list-style-type: none;

    + .task-list-item {
      margin-top: 0.25rem;
    }
  }

  .task-list-item-checkbox {
    margin: 0 0.5em 0.25em -1.4em;
    vertical-align: middle;
  }
}
