@use "abstracts/animations/scroll-top" as *;

.scroll-top {
  position: fixed;
  right: 15px;
  bottom: 15px;
  z-index: 99999;
  background-color: var(--bgColor-accent-emphasis);
  width: 44px;
  height: 44px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;

  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(20px);

  transition: background-color 0.3s ease, opacity 0.3s ease, transform 0.3s ease;
  will-change: background-color, opacity, transform;

  animation-timing-function: ease-out;

  &:hover {
    background-color: color-mix(in srgb, var(--bgColor-accent-emphasis), transparent 20%);
    color: var(--fgColor-onEmphasis);
  }

  &[data-state="visible"] {
    animation: scrollTopBounceIn 0.4s both;
    pointer-events: auto;
    opacity: 1;
  }

  &[data-state="hidden"] {
    animation: scrollTopBounceOut 0.4s both;
    pointer-events: none;
    opacity: 0;
  }

  svg {
    fill: var(--fgColor-onEmphasis);
  }
}
