@use "abstracts/modules/no-cursor" as *;
@use "abstracts/modules/text-truncate" as *;

.post-content {
  @extend %no-cursor;

  h2 {
    border-bottom: 1px solid var(--borderColor-default);
    padding-bottom: 0.25rem;
  }
}

.posts {
  display: flex;
  flex-direction: column;
  gap: clamp(1rem, 2vw, 1.5rem);
  margin: 0;
  padding: 0;

  .post {
    padding: clamp(1rem, 2.5vw, 1.5rem);
    background-color: var(--bgColor-default);
    border-radius: 0.5rem;
    border: 1px solid var(--borderColor-default);
    display: flex;
    flex-direction: column;
    gap: clamp(0.5rem, 1vw, 0.75rem);
  }

  .post-header {
    display: flex;
    flex-direction: column;
    gap: clamp(0.4rem, 1vw, 0.75rem);
  }

  .post-title {
    margin: 0;
    max-width: 60%;

    @extend %text-truncate;

    @media (max-width: 768px) {
      max-width: 100%;
    }
  }

  .post-excerpt {
    margin: 0;
    color: var(--fgColor-muted);
  }

  .post-footer {
    margin-top: 0;
  }
}

.post-meta {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: clamp(0.5rem, 1vw, 1rem) clamp(1rem, 2vw, 1.5rem);
  font-size: clamp(0.85rem, 0.8vw + 0.5rem, 0.95rem);
  color: var(--fgColor-muted);

  > div {
    display: inline-flex;
    align-items: center;
    gap: 0.25rem;

    svg {
      width: 1.5em;
      height: 1.5em;
    }
  }

  @media (max-width: 600px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.post-footer {
  margin-top: clamp(2rem, 5vw, 3rem);
  font-size: clamp(0.85rem, 1vw, 1rem);
  color: var(--fgColor-muted);

  .post-author {
    span[itemprop="name"] {
      color: var(--fgColor-default);
      font-weight: 500;
    }
  }

  .post-footer-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;

    .copyright {
      flex: 1 1 auto;
    }

    .share-icons {
      display: flex;
      gap: clamp(0.25rem, 1vw, 0.5rem);
      align-items: center;

      a {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        padding: clamp(0.25rem, 0.5vw, 0.5rem);
        transition: color 0.2s;

        svg {
          width: clamp(1rem, 1.2vw, 1.4rem);
          height: clamp(1rem, 1.2vw, 1.4rem);
        }

        .icon-twitter { fill: #1DA1F2; }
        .icon-facebook { fill: #1877F2; }
        .icon-telegram { fill: #0088cc; }
        .icon-linkedin { fill: #0A66C2; }
        .icon-whatsapp { fill: #25D366; }
      }
    }

    @media (max-width: 768px) {
      flex-direction: column;
      align-items: flex-start;
      text-align: left;
    }
  }
}
