#progress-bar {
  position: fixed;
  top: 0;
  left: 0;
  height: 0.25rem;
  width: 0%;
  background-color: var(--bgColor-accent-emphasis);
  z-index: 9999;
  transition: width 0.3s ease, opacity 0.5s ease;
  opacity: 1;
  pointer-events: none;

  &[data-state="done"] {
    opacity: 0;
    transition: opacity 0.5s ease-in;
  }
}
