@keyframes scrollTopBounceIn {
  from {
    opacity: 0;
    transform: translateY(20px) scale(0.95);
    visibility: hidden;
  }
  60% {
    opacity: 1;
    transform: translateY(-5px) scale(1.05);
    visibility: visible;
  }
  to {
    transform: translateY(0) scale(1);
    visibility: visible;
  }
}

@keyframes scrollTopBounceOut {
  from {
    opacity: 1;
    transform: translateY(0) scale(1);
    visibility: visible;
  }
  to {
    opacity: 0;
    transform: translateY(20px) scale(0.95);
    visibility: hidden;
  }
}
